<template>
  <div>
    <h1 class="title">:</h1>
    <el-table :data="items" border style="width: 100%">
      <el-table-column prop="userName" label=""></el-table-column>
      <el-table-column prop="passWord" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="120">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
          <el-button
            @click="$router.push(`/user/edit/${scope.row._id}`)"
            type="text"
            size="small"
          ></el-button>
          <el-button @click="remove(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "mcList",
  data() {
    return { items: [] };
  },
  methods: {
    async fetch() {
      const data = await this.$http.get("/rest/user");
      this.items = data.data;
      console.log(data);
    },

    async remove(row) {
      this.$confirm(` ${row._id} ,`, "", {
        distinguishCancelAndClose: true,
        confirmButtonText: "",
        cancelButtonText: ""
      }).then(async () => {
        console.log(row._id);
        const data = await this.$http.delete(`/rest/user/${row._id}`);
        this.$notify({
          title: "",
          type: "success",
          message: ""
        });
        this.fetch();
      });
    }
  },
  created() {
    this.fetch();
  }
};
</script>

