<template>
  <div class="loginPage">
    <div class="loginBg"></div>
    <div class="login">
      <el-card header="" class="box-card">
        <el-form @submit.native.prevent="login">
          <el-form-item>
            <el-input placeholder="" v-model="model.userName"></el-input>
          </el-form-item>
          <el-form-item>
            <el-input placeholder="" type="password" v-model="model.passWord"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button class="SubBtn" type="primary" native-type="submit"></el-button>
          </el-form-item>
          <el-button class="ForgetPass" type="text">?</el-button>
        </el-form>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      model: {}
    };
  },
  methods: {
    async login() {
      if (!this.model.userName && !this.model.passWord) {
        this.$message({
          type: "error",
          message: ""
        });
        return;
      }
      let res = await this.$http.post("/user/login", this.model);
      //sessionStorage.token = res.data.token;  //token
      localStorage.token = res.data; //token
      this.$router.push("/");
      this.$message({
        type: "success",
        message: ""
      });
      console.log(localStorage.token);
    }
  }
};
</script>

<style scoped>
.loginPage {
  width: 100vw;
  height: 100vh;
}

.loginBg {
  float: left;
  position: absolute;
  background: url("../../assets/login.jpg") no-repeat fixed top;
  width: 100vw;
  height: 100vh;
  background-size: cover;
  -webkit-filter: blur(15px);
  -moz-filter: blur(15px);
  -o-filter: blur(15px);
  -ms-filter: blur(15px);
  filter: blur(0px);
}

.login {
  width: 400px;
  float: right;
  position: relative;
  margin-top: 23vh;
  margin-right: 10vw;
}

.box-card {
  color: #34465c !important;
  background: rgba(250, 250, 250, 0.9);
  border: 1px solid #dad4cd;
}

.SubBtn {
  width: 100%;
}

.ForgetPass {
  float: right;
}
</style>