/* eslint-disable no-console */
<template>
  <div class="Mc">
    <h1 class="title">:</h1>
    <el-form
      :rules="rules"
      :model="model"
      ref="model"
      label-width="120px"
      @submit.native.prevent="save('model')"
      name="CreateProject"
    >
      <el-form-item label="" prop="projectName">
        <el-input class="projectName" maxlength="10" v-model="model.projectName"></el-input>
      </el-form-item>
      <el-form-item label="" prop="projectFeatures">
        <el-input class="projectFeatures" v-model="model.projectFeatures"></el-input>
      </el-form-item>
      <el-form-item style="margin-top:1rem">
        <el-button class="sendProjectItem" type="primary" native-type="subumit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { VueEditor } from "vue2-editor";
export default {
  name: "createProject",
  props: {
    id: {}
  },
  data() {
    return {
      model: {},
      rules: {
        projectName: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 15, message: " 3  15 ", trigger: "blur" }
        ],
        projectFeatures: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 15, message: " 3  15 ", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    async fetchItem() {
      const res = await this.$http.get(`/rest/project/${this.id}`);
      this.model = res.data;
    },
    async save(formName) {
      await this.$refs[formName].validate(); //
      if (this.id) {
        await this.$http.put(`rest/project/${this.id}`, this.model);
      } else {
        await this.$http.post("rest/project", this.model);
        this.$notify({
          title: "",
          type: "success",
          message: ""
        });
      }
      this.$router.push("/project/list");
    }
  },
  created() {
    this.id && this.fetchItem();
  }
};
</script>

