<template>
  <div>
    <h1>Ac:</h1>
    <el-table
      :data="tableProps.slice((currentPage-1)*pagesize,currentPage*pagesize)"
      :default-sort="{prop: 'items', order: 'descending'}"
      style="width: 100%"
    >
      <el-table-column sortable prop="createTime" label=""></el-table-column>
      <el-table-column sortable prop="versionNumber" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="300">
        <template slot="header" slot-scope="scope">
          <el-input @input="SearchTable" v-model="search" size="mini" placeholder="" />
        </template>
        <template slot-scope="scope">
          <el-button @click="$router.push(`/ac/view/${scope.row._id}`)" type="text" size="small"></el-button>
          <el-button @click="$router.push(`/ac/edit/${scope.row._id}`)" type="text" size="small"></el-button>
          <el-button @click="remove(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 20, 40]"
      :page-size="pagesize"
      layout="total, prev, next, jumper"
      :total="items.length"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: "mcList",
  data() {
    return {
      items: [],
      search: "",
      tableProps: [],
      currentPage: 1, //
      pagesize: 10 //    
    };
  },
  methods: {
    handleSizeChange: function(size) {
      this.pagesize = size;
      console.log(this.pagesize); //
    },

    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
      console.log(this.currentPage); //
      this.tableProps = this.items;
    },

    async fetch() {
      const data = await this.$http.get("/rest/ac");
      this.items = data.data;
      this.tableProps = this.items;
      console.log(data);
    },

    SearchTable() {
      let data = this.items.filter(
        data =>
          !this.search ||
          data.versionNumber.toLowerCase().includes(this.search.toLowerCase())
      );
      this.tableProps = data;
    },

    async remove(row) {
      this.$confirm(
        ` ${row.versionNumber} ,`,
        "",
        {
          distinguishCancelAndClose: true,
          confirmButtonText: "",
          cancelButtonText: ""
        }
      )
        .then(async () => {
          await this.$http.delete(`/rest/Ac/${row._id}`);
          if (row.fileName == " " || row.fileName == undefined) {
            //
            await this.$http.delete(`/deleteFile/${row.fileName}`);
          }
          this.fetch();
          this.$notify({
            title: "",
            type: "success",
            message: ""
          });
        })
        .catch(err => {
          console.log(``, err);
        });
    }
  },
  created() {
    this.fetch();
  }
};
</script>

